import interfaceurl from '@/utils/interface.js'
export default {
	data() {
		return {
			recommendList: [], //好玩推荐
			downloadList: [], //人气下载
			latestList: [], //最新游戏
			monthlyList: [], //月榜排行
			params: {
				typeId: '', //产品类型ID
				searchContent: '', //搜素内容
				startDT: '', //开始时间
				endDT: '', //结束时间
				start: 0, //开始行
				length: 10, //每页数据量
				type: 1 //查询类别：1:好玩推荐2:最新上架3:下载热榜4:好评度高5:月度排行
			} //接口参数
		}
	},
	onShow() {
		uni.showLoading();
		this.getRecommendList();
		this.getDownloadList();
		this.getLatestList();
		this.getMonthlyList();
		uni.hideLoading();
	},
	onLoad() {
		
	},
	methods: {
		//跳转宝贝详情页，传入宝贝id
		turn(id) {
			this.$turnPage('../shop/cart/goodsdetail?id=' + id, 'navigateTo');
		},
		//获取所有类型数据
		getRecommendList() {
			let that = this;
			that.params.type = 1;
			interfaceurl.checkAuth(interfaceurl.productPage, that.params, false).then((res) => {
				this.recommendList = res.data;
			});
		},
		getDownloadList() {
			let that = this;
			that.params.type = 3;
			interfaceurl.checkAuth(interfaceurl.productPage, that.params, false).then((res) => {
				this.downloadList = res.data;
			});
		},
		getLatestList() {
			let that = this;
			that.params.type = 2;
			interfaceurl.checkAuth(interfaceurl.productPage, that.params, false).then((res) => {
				this.latestList = res.data;
			});
		},
		getMonthlyList() {
			let that = this;
			that.params.type = 5;
			interfaceurl.checkAuth(interfaceurl.productPage, that.params, false).then((res) => {
				this.monthlyList = res.data;
			});
		}
	}
}
